import uniStatusBar from "../../components/uni-ui/uni-status-bar/uni-status-bar.vue"
export default {
	components: {
		uniStatusBar
	},
	data() {

		return {
			check: true,
			password: '',
			username: '',
			// 验证规则
			rules: {
				username: [{
						rule: /^[a-zA-Z]\w{5,17}$/,
						msg: "以字母开头,长度在6~18之间,只能包含字母,数字和下划线"
					},

				],
				password: [{
					rule: /^.{5,20}$/,
					msg: "密码长度为5-20的所有字符"
				}]
			},
			focus: {
				username: false,
				password: false
			}

		}
	},
	onLoad() {
		console.log(this.focus.username);
	},
	methods: {
		// 表单验证
		validate(key) {
			var check = true
			this.rules[key].forEach((v) => {
				if (!(v.rule).test(this[key])) {
					uni.showToast({
						title: v.msg,
						icon: "none"
					});
					console.log(v.msg);
					check = false
					return false
				}
			})
			return check
		},
		// 提交表单
		submit() {
			if (!this.check) {
				return uni.showToast({
					title: "请同意xx协议",
					icon: "none"
				})
			}

			// 验证码用户名
			if (!this.validate('username')) return

			if (!this.validate('password')) return

			uni.showLoading({
				title: '登录中...',
				mask: true
			})
			
			setTimeout(function() {
				uni.hideLoading()
				uni.navigateBack({
					
				})
			}, 3000);
		},
		goback() {
			uni.navigateBack({

			})
		},
		forget() {

		},
		focuss(key) {
			console.log(key);
			this.focus[key] = true
			// console.log(this.focus['username']);

		},
		blur(key) {
			this.focus[key] = false
		},


	}
}
